<template>
  <div>
    <v-form @submit.prevent="onSend">
      <container-form title="Productos" isForm @onReset="clear">
        <v-col cols="12" sm="6" md="4" xl="3">
          <v-select
            :items="presentaciones"
            v-model="producto.id_presentacion"
            item-value="id"
            item-text="nombre"
            label="Presentación"
            color="red lighten-3"
            item-color="red lighten-3"
          ></v-select>
        </v-col>

        <v-col cols="12" sm="6" md="4" xl="3">
          <v-select
            :items="categorias"
            v-model="producto.id_categoria"
            item-value="id"
            item-text="nombre"
            label="Categoria"
            color="red lighten-3"
          >
          </v-select>
        </v-col>

        <v-col cols="12" sm="6" md="4" xl="3">
          <v-text-field
            v-model="producto.codigo"
            label="Código"
            color="red lighten-3"
          ></v-text-field>
        </v-col>

        <v-col cols="12" sm="6" md="4" xl="3">
          <v-text-field
            v-model="producto.nombre"
            label="Producto"
            color="red lighten-3"
          ></v-text-field>
        </v-col>

        <v-col cols="12" sm="6" md="4" xl="3">
          <v-text-field
            type="number"
            v-model="producto.precio"
            label="Precio del producto"
            color="red lighten-3"
          ></v-text-field>
        </v-col>

        <v-col cols="12" sm="6" md="4" xl="3">
          <v-text-field
            type="number"
            v-model="producto.precio_compra"
            label="Precio compra del producto"
            color="red lighten-3"
          ></v-text-field>
        </v-col>
      </container-form>
    </v-form>

    <container-form>
      <v-col cols="12">
        <table-form url="producto"></table-form>
      </v-col>
    </container-form>
  </div>
</template>

<script>
/* Componentes */
import ContainerForm from "./../helpers/ContainerForm.vue";
import TableForm from "./../helpers/TableForm.vue";
/* Service */
import ProductoService from "./../../service/producto/index.js";

export default {
  name: "Producto",
  components: {
    ContainerForm,
    TableForm,
  },
  async mounted() {
    this.presentaciones = await ProductoService.getPresentaciones();
    this.categorias = await ProductoService.getCategorias();
  },
  data() {
    return {
      presentaciones: [],
      categorias: [],
      producto: {
        id_presentacion: null,
        id_categoria: null,
        codigo: "#",
        nombre: null,
        precio: null,
        precio_compra: null,
      },
    };
  },
  methods: {
    onSend() {
      console.log(this.producto);
      ProductoService.setProducto(this.producto);
      //let data = service.setData("producto", this.producto);
    },
    clear() {
      this.producto = {
        id_presentacion: null,
        id_categoria: null,
        codigo: "#",
        producto: null,
        precio: null,
        precio_compra: null,
      };
    },
  },
};
</script>
