<script>
export default {
  name: "FormLogin",
  data() {
    return {
      valid: false,
      correo: null,
      password: null,
      correoRules: [
        (v) => !!v || "Correo es requerido.",
        (v) => /.+@.+/.test(v) || "El correo no es válido.",
      ],
