import axios from "axios";

const axios_config = axios.create({
  baseURL: process.env.VUE_APP_URL_BASE,
});

const getAll = async (ruta) => {
  let data = await axios_config.get(ruta);
  return data.data;
};

async function setData(ruta, obj) {
  let data = await axios_config.post(ruta, obj);
  return data;
}

export default {
  getAll: getAll,
  setData: setData,
};
