<template>
  <div>
    <v-simple-table v-show="countProductos">
      <template v-slot:default>
        <thead>
          <tr>
            <th>#</th>
            <th class="text-left">
              Producto
            </th>
            <th class="text-left">
              Precio Venta
            </th>
            <th class="text-left">
              Precio Compra
            </th>
            <th class="text-left">
              Stock
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(producto, index) in productos" :key="producto.id">
            <td>{{ index + 1 }}</td>
            <td>{{ producto.nombre }}</td>
            <td>{{ producto.precio }}</td>
            <td>{{ producto.precio_compra }}</td>
            <td>{{ producto.stock }}</td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

    <v-skeleton-loader
      v-show="!countProductos"
      class="mx-auto"
      type="table-row"
    ></v-skeleton-loader>
    <v-pagination
      color="red lighten-3"
      v-model="pages"
      :length="total"
      v-show="countProductos"
      @next="Page"
      @previous="Page"
      @input="Page"
      circle
    ></v-pagination>
  </div>
</template>

<script>
import service from "./../../service/index.js";

export default {
  name: "TableForm",
  props: {
    url: String,
  },
  async mounted() {
    //Acá ire la llamada a api rest
    //process.env.VUE_APP_URL_BASE -> variables env
    this.getData();
  },
  data() {
    return {
      total: 0,
      productos: [],
      pages: 1,
    };
  },
  methods: {
    async Page() {
      this.getData();
    },

    /* Metodo para cargar los datos */
    async getData() {
      this.productos = [];
      let data = await service.getAll(`${this.url}?page=${this.pages}`);
      this.productos = data.data;
      this.total = this.total == 0 ? data.last_page : this.total;
    },
  },
  computed: {
    countProductos() {
      return this.productos.length >= 0;
    },
  },
};
</script>
